	module.exports = {"data":{"markdownRemark":{"frontmatter":{"title":"TEREM","type":"lyrics"},"html":"<h1>ТЕРЕМ</h1>\n<section>\n<p>Время говорило со мной на японском,<br>\nСжимало меня в иероглиф оно.</p>\n<p>Время показало мне, как оставаться плоским,<br>\nНо я непослушный сматывался веретеном.</p>\n<p>Тянется путь<br>\nмеченой тенью,<br>\nя ищу терем,<br>\nспособный обуть.</p>\n<p>Мне казалось, я — это только намёки,<br>\nПростые мотивы, чужие слова.</p>\n<p>Но дорога говорила мне, что в ливень мы не<br>\nнамокнем.<br>\nВода барабанит о руки мои — жернова.</p>\n<p>Тянется путь<br>\nмеченой тенью,<br>\nя ищу терем, способный обуть.</p>\n<p>Время показало мне двенадцать ударов,<br>\nно я не почувствовал ни одного.</p>\n<p>Я выпал в окно, подражая Икару — <br>\nпод кожей моей перьями дрожало стекло.</p>\n<p>теплится время<br>\nпуть мой наручник<br>\nсветит сквозь ночь мне<br>\nброшенный терем<br>\nтерем дом<br>\nтерем который<br>\nспособен обуть<br>\nя крадусь вором<br>\nбоюсь вздохнуть</p>\n</section>"},"site":{"siteMetadata":{"languages":{"defaultLangKey":"en","langs":["en","ru"]}}}},"pathContext":{"slug":"/en/lyrics/terem/"}}

